.app {
	position: relative;
	width: 100%;
	min-width: 575px;
	max-width: 620px;
	margin: 45px auto;
	padding: 50px;
	border-radius: 44px;
	text-align: center;
	background: #6f00ff;
	background-color: #6f00ff;
	box-shadow: 0 4.2px 2px #5900cc, 0 -4.2px 2px #8500ff;
	.app-heading {
		margin: 0 0 34px;
		color: #fff;
		font-weight: 700;
		font-size: 30px;
		text-align: center;
		text-shadow: 0 3px 0 #a875e9;
		letter-spacing: 0.19px;
		-webkit-text-stroke: 1px #fff;
	}
	.app-reset {
		position: absolute;
		top: 48px;
		right: 49px;
		cursor: pointer;
	}
	.app-scores {
		& > * {
			display: inline-block;
			vertical-align: middle;
		}
		& > .app-versus {
			margin: 0 7px;
			color: #fff;
			font-size: 64px;
		}
	}
	.app-box {
		margin: 26px 0 23px;
	}
	.box {
		padding: 5px;
		box-shadow: 0 3px 8px 0 rgba(56, 15, 110, 0.44);
		.box-inner {
			width: 100%;
			max-width: 520px;
			border: 6px solid #6e08fc;
			box-shadow: inset 0 3px 8px 0 rgba(56, 15, 110, 0.44);
			.app-hands {
				& > * {
					display: inline-block;
					vertical-align: middle;
				}
				& > .app-versus {
					color: #fff;
					font-weight: 700;
					font-size: 22px;
					text-shadow: 0 3px 0 hsla(0, 0%, 87.5%, 0.5);
					letter-spacing: -0.33px;
				}
			}
			.app-bet {
				margin-top: -15px;
				color: #fff;
				font-size: 17px;
				& > * {
					vertical-align: middle;
				}
				& > input {
					width: 73px;
					margin: 0 9px;
					padding: 14px 10px;
					border: 2px solid #753cf7;
					border-radius: 10px;
					color: #fff;
					font-size: 17px;
					background-color: #6000db;
				}
			}
			.app-history {
				margin: 20px 40px 36px;
				padding: 30px 47px;
				border-radius: 19px;
				color: #625f63;
				background-color: #fff;
				box-shadow: 0 3px 8px 0 rgba(89, 72, 112, 0.44),
					0 5px 3px rgba(215, 189, 248, 0.65);

				& > h2 {
					margin: 0;
					color: #6000db;
					font-weight: 700;
					font-size: 16px;
					letter-spacing: -0.24px;
				}
			}
		}
	}
	.box,
	.box-inner {
		border-radius: 35px;
	}
}
