.score {
	display: inline-block;
	min-width: 130px;
	height: 130px;
	padding: 28px 35px;
	border-radius: 27px;
	background-color: #6000db;
	box-shadow: inset 0 2px 3px rgba(0, 0, 0, 0.07);
}

.score-num {
	color: #fc0;
	font-weight: 700;
	font-size: 47px;
	line-height: 1;
	text-align: center;
	text-shadow: 0 3px 0 #af8d07;
}

.score-name {
	margin-top: 14px;
	color: #9680ff;
	font-weight: 700;
	font-size: 15px;
	font-family: GmarketSans;
	letter-spacing: -0.3px;
}
